.menu {
  display: none;

  @media (max-width: 800px) {
    display: block;
    background-color: #ffffff;
    border-radius: 50px;
  }
}

.menu__top {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin: 60px 0;
  padding-top: 20px;
  border-top: 1px solid #000000;

  &--santeh {
    margin-bottom: 23px;
  }
}

.menu__picture {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.menu__name {
  margin-top: 12px;
}

.menu__nav-item {
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.menu__nav {
  padding-top: 20px;
  border-top: 1px solid #000000;
  margin-bottom: 70px;
}

.menu__bottom  {
  padding-top: 20px;
  border-top: 1px solid #000000;
  margin-bottom: 20px;
}

.menu__lang-name {
  text-transform: uppercase;
}

.menu__santeh-link {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.menu__santeh  {
  position: relative;
}

.menu__tile-link {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.menu__tile {
  position: relative;
}